@import 'fontvar';

@font-face {
  font-family: '#{$icomoon-font-family}';
  src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?wp9a3t');
  src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?wp9a3t#iefix') format('embedded-opentype'), url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?wp9a3t') format('truetype'), url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?wp9a3t') format('woff'), url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?wp9a3t##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='_icon-']::after,
[class*=' _icon-']::after {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

